// ContactForm.tsx
import React, { useState } from 'react';
import './contact.css';
import { FaUser, FaEnvelope, FaPhone, FaHome, FaClock, FaPaperPlane, FaServicestack } from 'react-icons/fa';

const ContactForm = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    service: '',
    message: ''
  });
  
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitSuccess, setSubmitSuccess] = useState(false);
  
  const services = [
    "Kitchen Remodeling",
    "Bathroom Remodeling",
    "Room Additions",
    "Basement Finishing",
    "Outdoor Living Spaces",
    "Whole Home Renovation",
    "Windows & Doors",
    "Roofing & Siding"
  ];

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    
    // Simulate form submission
    setTimeout(() => {
      setIsSubmitting(false);
      setSubmitSuccess(true);
      setFormData({ name: '', email: '', phone: '', service: '', message: '' });
      
      // Reset success message after 5 seconds
      setTimeout(() => setSubmitSuccess(false), 5000);
    }, 1500);
  };

  return (
    <section className="contact-section" id="contact">
      <div className="section-header">
        <h2 className="section-title">Contact Us</h2>
        <div className="divider"></div>
        <p className="section-subtitle">Get in touch for a free consultation and estimate</p>
      </div>
      
      <div className="contact-container">
        <div className="contact-info">
          <div className="info-card">
            <h3 className="info-title">Contact Information</h3>
            
            <div className="contact-item">
              <div className="contact-icon">
                <FaHome />
              </div>
              <div className="contact-details">
                <h4>Our Location</h4>
                <p>123 Construction Way, Nampa, ID 83651</p>
              </div>
            </div>
            
            <div className="contact-item">
              <div className="contact-icon">
                <FaPhone />
              </div>
              <div className="contact-details">
                <h4>Phone Number</h4>
                <p>(120)8808-8576</p>
                <p className="contact-note">Mon-Sat: 8am-10pm</p>
              </div>
            </div>
            
            <div className="contact-item">
              <div className="contact-icon">
                <FaEnvelope />
              </div>
              <div className="contact-details">
                <h4>Email Address</h4>
                <p>gwhandyman@outlook.com</p>
                <p className="contact-note">Response within 24 hours</p>
              </div>
            </div>
            
            <div className="contact-item">
              <div className="contact-icon">
                <FaClock />
              </div>
              <div className="contact-details">
                <h4>Business Hours</h4>
                <p>Monday - Saturday: 8am - 10pm</p>
                <p>Sunday: Closed</p>
              </div>
            </div>
          </div>
          
          
        </div>
        
        <div className="contact-form">
          <div className="form-header">
            <h3>Send us a message</h3>
            <p>Fill out the form below and we'll get back to you as soon as possible</p>
          </div>
          
          {submitSuccess && (
            <div className="success-message">
              <p>Your message has been sent successfully!</p>
              <p>We'll contact you within 24 hours.</p>
            </div>
          )}
          
          <form onSubmit={handleSubmit}>
            <div className="form-group">
              <div className="input-icon">
                <FaUser />
              </div>
              <input
                type="text"
                name="name"
                placeholder="Your Name"
                value={formData.name}
                onChange={handleChange}
                required
              />
            </div>
            
            <div className="form-group">
              <div className="input-icon">
                <FaEnvelope />
              </div>
              <input
                type="email"
                name="email"
                placeholder="Your Email"
                value={formData.email}
                onChange={handleChange}
                required
              />
            </div>
            
            <div className="form-group">
              <div className="input-icon">
                <FaPhone />
              </div>
              <input
                type="tel"
                name="phone"
                placeholder="Your Phone Number"
                value={formData.phone}
                onChange={handleChange}
                required
              />
            </div>
            
            <div className="form-group">
                <div className="input-icon">
                <FaServicestack />
              </div>
              <select
                name="service"
                value={formData.service}
                onChange={handleChange}
                required
              >
                <option value="">Select a Service</option>
                {services.map((service, index) => (
                  <option key={index} value={service}>{service}</option>
                ))}
              </select>
            </div>
            
            <div className="form-group">
              <textarea
                name="message"
                placeholder="Your Message"
                value={formData.message}
                onChange={handleChange}
                required
                rows={5}
              ></textarea>
            </div>
            
            <button 
              type="submit" 
              className="submit-btn"
              disabled={isSubmitting}
            >
              {isSubmitting ? 'Sending...' : (
                <>
                  <FaPaperPlane /> Send Message
                </>
              )}
            </button>
          </form>
        </div>
      </div>
    </section>
  );
};

export default ContactForm;