import React, { useState } from 'react';
import './faq.css';

const FAQSection = () => {
  const [activeIndex, setActiveIndex] = useState<number | null>(null);

  const toggleFAQ = (index: number) => {
    setActiveIndex(activeIndex === index ? null : index);
  };

  const faqs = [
    {
      question: "Do you charge for providing estimates for construction and remodeling projects?",
      answer: "No, we offer free estimates for all of our construction and remodeling services."
    },
    {
      question: "How is Good Works R&C different from other construction and remodeling companies?",
      answer: "Good Works R&C stands out with our commitment to quality craftsmanship, attention to detail, and excellent customer service. Additionally, we prioritize clear communication throughout the entire project to ensure our clients' vision is brought to life."
    },
    {
      question: "What services do you provide?",
      answer: "Good Works R&C offers a wide range of construction and remodeling services including kitchen and bathroom remodeling, room additions, roofing, siding, windows and doors installation, and general home improvement projects."
    }
  ];

  return (
    <section className="faq-section">
      <div className="faq-header">
        <h2 className="faq-title">Frequently Asked Questions</h2>
        <div className="divider"></div>
        <p className="faq-subtitle">Find answers to common questions about our services and processes</p>
      </div>
      
      <div className="faq-container">
        {faqs.map((faq, index) => (
          <div 
            key={index} 
            className={`faq-card ${activeIndex === index ? 'active' : ''}`}
            onClick={() => toggleFAQ(index)}
          >
            <div className="faq-question-container">
              <h3 className="faq-question">{faq.question}</h3>
              <div className="faq-icon">
                <div className={`icon-bar horizontal ${activeIndex === index ? 'active' : ''}`}></div>
                <div className={`icon-bar vertical ${activeIndex === index ? 'active' : ''}`}></div>
              </div>
            </div>
            
            <div className={`faq-answer ${activeIndex === index ? 'active' : ''}`}>
              <p>{faq.answer}</p>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};

export default FAQSection;