import React, { useState, useEffect, useRef } from 'react';
import './testimonialSlider.css';
import { FaGoogle, FaFacebookF, FaStar, FaRegStar, FaQuoteLeft, FaChevronLeft, FaChevronRight } from 'react-icons/fa';

interface Testimonial {
  id: number;
  source: 'google' | 'facebook';
  name: string;
  rating: number;
  review: string;
  date: string;
}

const testimonials: Testimonial[] = [
  {
    id: 1,
    source: 'google',
    name: 'Sarah Johnson',
    rating: 5,
    review: 'Exceptional service! The team went above and beyond to deliver outstanding results. Their attention to detail and professionalism exceeded my expectations.',
    date: '2 days ago'
  },
  {
    id: 2,
    source: 'facebook',
    name: 'Michael Chen',
    rating: 4,
    review: 'Great experience overall. The design was modern and functional. The team was responsive and delivered on time. Would definitely recommend!',
    date: '1 week ago'
  },
  {
    id: 3,
    source: 'google',
    name: 'Emily Rodriguez',
    rating: 5,
    review: 'Absolutely fantastic! They transformed my vision into reality with precision and creativity. The final product was better than I imagined.',
    date: '3 days ago'
  },
  
];

const TestimonialSlider: React.FC = () => {
  const [currentSlide, setCurrentSlide] = useState(0);
  const [slidesToShow, setSlidesToShow] = useState(1);
  const [isAutoPlaying, setIsAutoPlaying] = useState(true);
  const sliderRef = useRef<HTMLDivElement>(null);
  
  // Handle responsive slides
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth >= 1024) {
        setSlidesToShow(3);
      } else if (window.innerWidth >= 768) {
        setSlidesToShow(2);
      } else {
        setSlidesToShow(1);
      }
    };

    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const totalSlides = Math.ceil(testimonials.length / slidesToShow);
  
  // Auto slide functionality
  useEffect(() => {
    if (!isAutoPlaying) return;
    
    const interval = setInterval(() => {
      setCurrentSlide(prev => (prev + 1) % totalSlides);
    }, 5000);
    
    return () => clearInterval(interval);
  }, [totalSlides, isAutoPlaying]);

  const goToSlide = (index: number) => {
    if (index >= 0 && index < totalSlides) {
      setCurrentSlide(index);
    }
  };

  const nextSlide = () => {
    setCurrentSlide(prev => (prev + 1) % totalSlides);
    setIsAutoPlaying(false);
    setTimeout(() => setIsAutoPlaying(true), 10000);
  };

  const prevSlide = () => {
    setCurrentSlide(prev => (prev - 1 + totalSlides) % totalSlides);
    setIsAutoPlaying(false);
    setTimeout(() => setIsAutoPlaying(true), 10000);
  };

  // Render star rating
  const renderStars = (rating: number) => {
    return (
      <div className="star-rating">
        {[...Array(5)].map((_, i) => (
          i < rating ? 
            <FaStar key={i} className="star-icon filled" /> : 
            <FaRegStar key={i} className="star-icon" />
        ))}
      </div>
    );
  };

  return (
    <div 
      className="testimonial-slider" 
      id='testimonial'
      ref={sliderRef}
      onMouseEnter={() => setIsAutoPlaying(false)}
      onMouseLeave={() => setIsAutoPlaying(true)}
    >
      <div className="slider-header">
        <h2>Customer Testimonials</h2>
        <div className="divider"></div>
        <p>See what our customers are saying about their experience</p>
      </div>

      <div className="slider-container">
        <div 
          className="testimonial-track" 
          style={{ 
            transform: `translateX(-${currentSlide * 100}%)`,
            width: `${totalSlides * 100}%`
          }}
        >
          {Array.from({ length: totalSlides }).map((_, slideIndex) => (
            <div 
              key={slideIndex} 
              className="testimonial-slide"
              style={{ width: `${100 / totalSlides}%` }}
            >
              <div className="slide-group">
                {testimonials
                  .slice(slideIndex * slidesToShow, slideIndex * slidesToShow + slidesToShow)
                  .map(testimonial => (
                    <div key={testimonial.id} className="testimonial-card">
                      <FaQuoteLeft className="quote-icon" />
                      
                      <div className="testimonial-header">
                        <div className={`review-icon ${testimonial.source}`}>
                          {testimonial.source === 'google' ? 
                            <FaGoogle className="platform-icon" /> : 
                            <FaFacebookF className="platform-icon" />
                          }
                        </div>
                        <span className="review-source">
                          {testimonial.source.charAt(0).toUpperCase() + testimonial.source.slice(1)}
                        </span>
                        {renderStars(testimonial.rating)}
                      </div>
                      
                      <p className="testimonial-content">
                        {testimonial.review}
                      </p>
                      
                      <div className="reviewer-info">
                        <div className="avatar">
                          <div className="avatar-initial">
                            {testimonial.name.charAt(0)}
                          </div>
                        </div>
                        <div className="reviewer-details">
                          <div className="reviewer-name">{testimonial.name}</div>
                          <div className="review-date">{testimonial.date}</div>
                        </div>
                      </div>
                    </div>
                  ))}
              </div>
            </div>
          ))}
        </div>
      </div>

      <div className="slider-controls">
        <button 
          className="control-btn prev-btn" 
          onClick={prevSlide}
          disabled={currentSlide === 0}
        >
          <FaChevronLeft />
        </button>
        <button 
          className="control-btn next-btn" 
          onClick={nextSlide}
          disabled={currentSlide === totalSlides - 1}
        >
          <FaChevronRight />
        </button>
      </div>

      <div className="slider-dots">
        {Array.from({ length: totalSlides }).map((_, index) => (
          <div 
            key={index}
            className={`dot ${index === currentSlide ? 'active' : ''}`}
            onClick={() => goToSlide(index)}
          />
        ))}
      </div>

      <div className="stats-container">
        <div className="stat-item">
          <div className="stat-value">4.9/5</div>
          <div className="stat-label">Average Rating</div>
        </div>
        <div className="stat-item">
          <div className="stat-value">98%</div>
          <div className="stat-label">Satisfaction Rate</div>
        </div>
        <div className="stat-item">
          <div className="stat-value">500+</div>
          <div className="stat-label">Happy Clients</div>
        </div>
      </div>
    </div>
  );
};

export default TestimonialSlider;