// src/components/CTASection.tsx
import React from 'react';
import './cta.css';

const CTASection = () => {
  return (
    <section className="cta-container">
      <div className="cta-content">
        <h2 className="cta-title">Ready to get started?</h2>
        <p className="cta-subtitle">Book an appointment today and get a free quote on your construction project.</p>
        <a href="#quote" className="cta-button-c">Get a Free Quote</a>
      </div>
    </section>
  );
};

export default CTASection;