// ProjectGallery.tsx
import React, { useState } from 'react';
import './project.css';

// Import your static images
import kitchen1 from '../../images/k1.webp';
import kitchen2 from '../../images/k2.webp';
import bathroom1 from '../../images/b1.webp';
import bathroom2 from '../../images/b2.webp';
import b3 from '../../images/b3.webp';
import b4 from '../../images/b4.webp';
import b5 from '../../images/b5.webp';
import b6 from '../../images/b6.webp';
import o1 from '../../images/out1.webp';
import o2 from '../../images/out2.webp';
import o3 from '../../images/out3.webp';
import int1 from '../../images/int1.webp';
import int2 from '../../images/int2.webp';

const ProjectGallery = () => {
  const [activeFilter, setActiveFilter] = useState('all');
  const [previewOpen, setPreviewOpen] = useState(false);
  const [currentProjectIndex, setCurrentProjectIndex] = useState(0);
  
  const projects = [
    {
      id: 1,
     
      category: "kitchen",
     
      image: kitchen1
    },
    {
      id: 2,
     
      category: "bathroom",
  
      image: bathroom1
    },
    {
      id: 9,
     
      category: "outdoor",
    
      image: o1
    },
    {
      id: 12,
     
      category: "interior",
      
      image: int1
    },
    {
      id: 13,
      
      category: "interior",
   
      image: int2
    },
    {
      id: 10,
     
      category: "outdoor",
    
      image: o2
    },
    {
      id: 3,
      
      category: "bathroom",
     
      image: b3
    },
    {
      id: 4,
     
      category: "bathroom",
    
      image: b5
    },
    {
      id: 5,
   
      category: "bathroom",
     
      image: b6
    },
    {
      id: 11,
     
      category: "outdoor",
     
      image: o3
    },
    {
      id: 6,
     
      category: "bathroom",
     
      image: b4
    },
    {
      id: 7,
  
      category: "kitchen",
    
      image: kitchen2
    },
    {
      id: 8,
   
      category: "bathroom",
      
      image: bathroom2
    }
  ];

  const filteredProjects = activeFilter === 'all' 
    ? projects 
    : projects.filter(project => project.category === activeFilter);

  const openPreview = (index: number) => {
    setCurrentProjectIndex(index);
    setPreviewOpen(true);
    document.body.style.overflow = 'hidden';
  };

  const closePreview = () => {
    setPreviewOpen(false);
    document.body.style.overflow = 'auto';
  };

  const navigatePreview = (direction: 'prev' | 'next') => {
    if (direction === 'prev') {
      setCurrentProjectIndex(prevIndex => 
        prevIndex === 0 ? filteredProjects.length - 1 : prevIndex - 1
      );
    } else {
      setCurrentProjectIndex(prevIndex => 
        prevIndex === filteredProjects.length - 1 ? 0 : prevIndex + 1
      );
    }
  };

  return (
    <section className="project-gallery" id="project">
      <div className="section-header">
        <h2 className="section-title">Our Project Gallery</h2>
        <div className="divider"></div>
        <p className="section-subtitle">Explore our recent construction and remodeling projects</p>
      </div>

      <div className="gallery-filters">
        <button 
          className={`filter-btn ${activeFilter === 'all' ? 'active' : ''}`}
          onClick={() => setActiveFilter('all')}
        >
          All Projects
        </button>
        <button 
          className={`filter-btn ${activeFilter === 'kitchen' ? 'active' : ''}`}
          onClick={() => setActiveFilter('kitchen')}
        >
          Kitchens
        </button>
        <button 
          className={`filter-btn ${activeFilter === 'bathroom' ? 'active' : ''}`}
          onClick={() => setActiveFilter('bathroom')}
        >
          Bathrooms
        </button>
        <button 
          className={`filter-btn ${activeFilter === 'interior' ? 'active' : ''}`}
          onClick={() => setActiveFilter('interior')}
        >
          Interior
        </button>
        <button 
          className={`filter-btn ${activeFilter === 'outdoor' ? 'active' : ''}`}
          onClick={() => setActiveFilter('outdoor')}
        >
          Outdoor
        </button>
      </div>

      <div className="gallery-grid">
        {filteredProjects.map((project, index) => (
          <div key={project.id} className="project-card">
            <div className="project-image">
              <img 
                src={project.image} 
                alt={project.title} 
                className="project-img"
              />
              <div className="category-tag">{project.category}</div>
              <div className="project-overlay">
                <div className="overlay-content">
                  <button 
                    className="preview-btn"
                    onClick={(e) => {
                      e.stopPropagation();
                      openPreview(index);
                    }}
                  >
                    Preview
                  </button>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>

      {previewOpen && (
        <div className="preview-modal">
          <div className="modal-overlay" onClick={closePreview}></div>
          <div className="modal-content">
            <button className="close-btn" onClick={closePreview}>
              &times;
            </button>
            
            <div className="modal-image-container">
              <img 
                src={filteredProjects[currentProjectIndex].image} 
                alt={filteredProjects[currentProjectIndex].title}
                className="modal-image"
              />
              
              <button 
                className="nav-btn prev-btn"
                onClick={(e) => {
                  e.stopPropagation();
                  navigatePreview('prev');
                }}
              >
                &lt;
              </button>
              
              <button 
                className="nav-btn next-btn"
                onClick={(e) => {
                  e.stopPropagation();
                  navigatePreview('next');
                }}
              >
                &gt;
              </button>
            </div>
            
            <div className="modal-info">
              <h3 className="modal-title">{filteredProjects[currentProjectIndex].title}</h3>
              <p className="modal-description">{filteredProjects[currentProjectIndex].description}</p>
              <div className="modal-category">{filteredProjects[currentProjectIndex].category}</div>
            </div>
          </div>
        </div>
      )}
    </section>
  );
};

export default ProjectGallery;