import React, { useState, useEffect } from 'react';
import { Link } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';
import './navbar.css';

import { MdEmail } from 'react-icons/md';

const Header = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 10);
    };

    window.addEventListener('scroll', handleScroll);

    if (mobileMenuOpen) {
      document.body.classList.add('menu-open');
    } else {
      document.body.classList.remove('menu-open');
    }

    return () => {
      window.removeEventListener('scroll', handleScroll);
      document.body.classList.remove('menu-open');
    };
  }, [mobileMenuOpen]);

  const navItems = [
    { name: 'Home', path: '/' },
    { name: 'About Us', path: '#about' },
    { name: 'Projects', path: '#project' },
    { name: 'Testimonials', path: '#testimonial' },
  ];

  return (
    <>
      <header
        className={`header ${isScrolled ? 'header-scrolled' : ''} ${mobileMenuOpen ? 'menu-open' : ''}`}
      >
        <div className="header-container">
          <Link to="/" className="logo-link">
            <StaticImage
              src="../../images/logp.webp"
              alt="Fleming Services LLC"
              placeholder="blurred"
              className="log-image"
              width={180}
            />
         
          </Link>

          {/* Desktop Navigation */}
          <nav className="desktop-nav">
            {navItems.map((item) => (
              <Link
                key={item.name}
                to={item.path}
                className="nav-link"
                activeClassName="nav-link-active"
              >
                {item.name}
              </Link>
            ))}
            <a href="#contact" aria-label="Contact Us" className="cta-button primary">Contact Us</a>
            <a href="#quote" aria-label="Get a Quote" className="cta-button secondary">Get a Quote</a>
          </nav>

          {/* Mobile Menu Button */}
          <div className="mobile-header-actions">
            <button
              className={`mobile-menu-button ${mobileMenuOpen ? 'open' : ''}`}
              onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
              aria-label={mobileMenuOpen ? "Close menu" : "Open menu"}
            >
              <span className="menu-line"></span>
              <span className="menu-line"></span>
              <span className="menu-line"></span>
            </button>
          </div>
        </div>
      </header>

      {/* Mobile Navigation */}
      <div className={`mobile-nav-wrapper ${mobileMenuOpen ? 'open' : ''}`}>
        <div
          className="mobile-nav-overlay"
          onClick={() => setMobileMenuOpen(false)}
          role="button"
          aria-label="Close menu"
          tabIndex={0}
        ></div>
        <div className={`mobile-nav ${mobileMenuOpen ? 'open' : ''}`}>
          <div className="mobile-nav-container">
            <div className="mobile-nav-header">
              <StaticImage
                src="../../images/logo.png"
                alt="Fleming Services LLC"
                placeholder="blurred"
                className="mobile-logo-image"
                width={160}
              />
              <button
                className="mobile-close-button"
                onClick={() => setMobileMenuOpen(false)}
                aria-label="Close menu"
              >
                <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
                  <path d="M18 6L6 18" stroke="white" strokeWidth="2" strokeLinecap="round" />
                  <path d="M6 6L18 18" stroke="white" strokeWidth="2" strokeLinecap="round" />
                </svg>
              </button>
            </div>

            <div className="mobile-nav-items">
              {navItems.map((item) => (
                <Link
                  key={item.name}
                  to={item.path}
                  className="mobile-nav-link"
                  activeClassName="mobile-nav-link-active"
                  onClick={() => setMobileMenuOpen(false)}
                >
                  {item.name}
                </Link>
              ))}
            </div>

            {/* Mobile Buttons */}
           <div className="mobile-contact-buttons">
  <a href="#contact" className="cta-button primary">Contact Us</a>
  <a href="#quote" className="cta-button secondary">Get a Quote</a>
</div>

            <div className="mobile-contact-info">
              <a href="tel:208-912-844" className="mobile-phone">
                <svg width="20" height="20" viewBox="0 0 24 24" fill="white">
                  <path d="M22 16.92v3a2 2 0 01-2.18 2 19.79 19.79 0 01-8.63-3.07 19.5 19.5 0 01-6-6 19.79 19.79 0 01-3.07-8.67A2 2 0 014.11 2h3a2 2 0 012 1.72c.127.96.362 1.903.7 2.81a2 2 0 01-.45 2.11l-1.27 1.27a16 16 0 006 6l1.27-1.27a2 2 0 012.11-.45c.907.339 1.85.574 2.81.7A2 2 0 0122 16.92z" />
                </svg>
                (208) 912-844
              </a>
              <a href="mailto:gwhandyman@outlook.com" className="mobile-email">
                <MdEmail />
                gwhandyman@outlook.com
              </a>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Header;
